.scroll__container {
	position: fixed;
	right: 30px;
	bottom: 30px;
	z-index: 1;
	#top {
		position: relative;
		border: 1px solid $borderColor;
		width: 70px;
		height: 40px;
		outline: none;
		background-color: $borderColor;
		opacity: 0.7;
		cursor: pointer;
		color: #fff;
		font-size: 14px;
	}
	#top:hover {
		//background-color: $hoverColor;
		opacity: 1;
	}
}

@media screen and (max-width: $tablet) {
  .scroll__container {
    #top {
      width: 50px;
      height: 30px;
      font-size: 12px;
    }
  }
}